import React, { Component } from "react";
import Timer from "./components/Timer";
import editableTitle from "./components/Title";
import chill from './chill.svg';
import work from './work.svg';
import celebrate from './celebrate.svg'

// import Controls from "./components/Controls";

let EditableTitle = editableTitle('Pomo');
class App extends Component {
  state = {
    onBreak: false,
    finish: false
  };
  changeBreak = () => {
    this.setState({
      onBreak: !this.state.onBreak
    })
  }
  Finished = () => {
    this.setState({
      finish: true
    })
  }
  unFinish = () => {
    this.setState({
      finish: false
    })
  }
  resetBreak = () => {
    this.setState({
      onBreak: false
    })
  }
  onChange = e => {
    this.setState({
      value: e.target.value
    });
  };
  render() {
    return (
      <div>
        <div className={!this.state.onBreak ? "dashboard" : "dashboard dashboard--break"}>
          {this.state.finish ?<img
            className="image"
            src={celebrate}
            alt="tomato"
          /> : 
          <img
            className="image"
            src={this.state.onBreak ? chill : work}
            alt="tomato"
          />}
          {/* <input
            onChange={this.onChange}
            value={this.state.value}
            type="text"
            placeholder={this.state.title}
          /> */}
          <EditableTitle value="My Pomodoro Timer"/>
          <Timer
          changeBreak={this.changeBreak}
          onBreak={this.state.onBreak}
          resetBreak={this.resetBreak}
          unFinish={this.unFinish}
          Finished={this.Finished}
          onFinish={this.state.finish}
          />
        </div>
      </div>
    );
  }
}

export default App;



