import React, { Component } from "react";
import Controls from "./Controls";

export default class Timer extends Component {
  state = {
    targetTime: 3, // Default 25 minutes per session
    breakTime: 3, // Default 5 minutes break
    elapsed: 0,
    session: 0,
    running: false,
    targetSession: 4, // Default 4 sessions
    intervalInput: "",
    sessionInput: "",
    breakInput: ""
  };
  setTime = () => {
    // Run this in interval to constantly update the time.
    this.setState({
      elapsed: this.state.elapsed + 1
    });
    if (
      !this.props.onBreak &&
      this.state.targetTime - this.state.elapsed === 0
    ) {
      this.setState({
        session: this.state.session + 1
      });
      // If target session is reached
      if (this.state.session !== this.state.targetSession) {
        // take a break!
        this.onBreak();
      } else {
        // Target met.
        this.props.Finished();
        this.stopTimer();
      }
    } else if (this.props.onBreak) {
      if (this.state.elapsed === this.state.breakTime) {
        clearInterval(this.setTimeInterval);
        this.onBreak();
        this.setState({
          // running: !this.state.running,
          elapsed: 0
        });
      }
    }
  };
  onBreak = () => {
    this.setState({
      elapsed: 0
    });
    const audio = new Audio(
      "http://soundbible.com/mp3/service-bell_daniel_simion.mp3"
    );
    audio.loop = false;
    audio.play();
    // setTimeout(audio.pause,2000);
    this.props.changeBreak();
    // this.stopTimer();
    clearInterval(this.setTimeInterval);
    this.setTimeInterval = setInterval(() => this.setTime(), 1000);
  };
  // Starts the timer, saves when the timer starts running
  startTimer = () => {
    if (this.state.targetSession !== this.state.session) {
      this.props.unFinish();
      this.setState({
        running: true
      });
      clearInterval(this.setTimeInterval);
      this.setTimeInterval = setInterval(() => this.setTime(), 1000);
    }
  };
  // setting the target time / interval
  setTarget = e => {
    if (this.state.intervalInput <= 99) {
      this.setState({
        targetTime: this.state.intervalInput * 60,
        intervalInput: "",
        elapsed: 0
      });
    }
    e.preventDefault();
  };
  setSession = e => {
    this.setState({
      targetSession: this.state.sessionInput * 1,
      sessionInput: ""
    });
    e.preventDefault();
  };

  setBreak = e => {
    this.setState({
      breakTime: this.state.breakInput * 60,
      breakInput: ""
    });
    e.preventDefault();
  };
  stopTimer = () => {
    clearInterval(this.setTimeInterval);
    this.setState({
      running: false
    });
  };
  resetTimer = () => {
    this.stopTimer();
    this.props.unFinish();
    this.setState({
      elapsed: 0,
      session: 0,
      running: false
    });
    this.props.resetBreak();
  };
  handleChangeInterval = e => {
    if (e.target.value.match(/^[1-9]{0,1}[0-9]{0,1}$/)) {
      if (document.activeElement === document.getElementById("interval")) {
        this.setState({
          intervalInput: e.target.value
        });
      } else if (
        document.activeElement === document.getElementById("session")
      ) {
        this.setState({
          sessionInput: e.target.value
        });
      } else {
        this.setState({
          breakInput: e.target.value
        });
      }
    }
  };
  render() {
    return (
      <div>
        <div className="timer">
          <p
            className={
              this.state.running | this.props.onBreak
                ? "timer__text timer__text--big"
                : "timer__text"
            }
          >
            {!this.props.onBreak
              ? "Time Remaining: " +
                Math.floor((this.state.targetTime - this.state.elapsed) / 60) +
                " min "
              : "Break: " +
                Math.floor((this.state.breakTime - this.state.elapsed) / 60) +
                " min "}
            {!this.props.onBreak
              ? Math.floor((this.state.targetTime - this.state.elapsed) % 60) +
                " sec"
              : Math.floor((this.state.breakTime - this.state.elapsed) % 60) +
                "sec"}
          </p>
          {!this.state.running && !this.props.onBreak ? (
            <p className="timer__text">
              Target session: {this.state.targetSession}
            </p>
          ) : (
            <p className="timer__text" />
          )}
          {!this.state.running && !this.props.onBreak ? (
            <p className="timer__text">
              Break: {Math.floor(this.state.breakTime / 60) + "min"}{" "}
              {Math.floor(this.state.breakTime % 60) + "sec"}
            </p>
          ) : (
            <p className="timer__text" />
          )}
          <p className="timer__text">Session no. {this.state.session}</p>
          {this.props.onFinish ? (
            <p className="timer__text--congratulation">
              {" "}
              Awesome! Go take a break now. If you want to continue, click
              reset.
            </p>
          ) : (
            ""
          )}
        </div>
        {/* Control panel here */}
        <div className="controls">
          <div className="btns">
            <button
              className={!this.state.running ? "btn" : "btn btn--running"}
              onClick={this.startTimer}
            >
              Start
            </button>
            <button
              className={!this.state.running ? "btn" : "btn btn--bigger"}
              onClick={this.stopTimer}
            >
              Stop!
            </button>
            <button
              className={!this.state.running ? "btn" : "btn btn--running"}
              onClick={this.resetTimer}
            >
              Reset!
            </button>
          </div>
          {!this.state.running ? (
            <div>
              <form className="form" onSubmit={this.setTarget}>
                <input
                  id="interval"
                  className="interval no-spin"
                  onChange={this.handleChangeInterval}
                  value={this.state.intervalInput}
                  type="text"
                  placeholder="Interval"
                />
                <input className="form__submit" type="Submit" value="Set" />
              </form>

              <form className="form" onSubmit={this.setSession}>
                <input
                  id="session"
                  className="interval no-spin"
                  onChange={this.handleChangeInterval}
                  value={this.state.sessionInput}
                  type="text"
                  placeholder="Sessions"
                />
                <input className="form__submit" type="Submit" value="Set" />
              </form>
              <form className="form" onSubmit={this.setBreak}>
                <input
                  id="break"
                  className="interval no-spin"
                  onChange={this.handleChangeInterval}
                  value={this.state.breakInput}
                  type="text"
                  placeholder="Break time"
                />
                <input className="form__submit" type="Submit" value="Set" />
              </form>
            </div>
          ) : (
            ""
          )}
        </div>
      </div>
    );
  }
}



