import React from "react";
import { NavLink } from "react-router-dom";

const Header = () => {
  const show = () => {
    alert(
      "Pomodoro timer is a useful technique used for improving your focus."
    );
  };
  return (
    <div className="navigation">
    <NavLink to="/pomodoro" className="btn btn--nav">
        Home
      </NavLink>
      <a onClick={show} className="btn btn--nav">
        What is this?
      </a>
      <NavLink to="/pomodoro/how" className="btn btn--nav">
        How to use
      </NavLink>
    </div>
  );
};

export default Header;



